export default function deepLink() {
  console.log('Entered Deeplink');

  const parameters = {};
  const url = new URL(window.location.href.replace('#', '?'));

  console.log('Entering Search');
  console.log('before', { url });
  if (url.search) {
    console.log('after', { url });

    url.search
      .substring(1)
      .split('&')
      .forEach(param => {
        parameters[param.split('=')[0]] = param.split('=')[1];
      });

    console.log('Back to App with:', parameters);

    if (parameters?.access_token) {
      const expireTime = new Date().setHours(0, 0, parameters.expires_in);
      document.cookie = `spotify_token=${parameters.access_token}; expires=${expireTime}`;
    }

    if (parameters?.code) {
      window.location = new URL(`poses101://?code=${parameters.code}`);
      console.log('Page url has code parameter, callback triggered', parameters.code);
    }
  }
}
