import React, { useEffect, useState } from 'react';
import Routes from './routes';
import HeaderBG from './views/header';
import deepLink from './products';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGem } from '@fortawesome/free-solid-svg-icons'
import './App.css';

function App() {
  const [cookieValue, setNewCookie] = useState(null);

  useEffect(() => {
    deepLink();
  });

  useEffect(() => {
    const cookie = document.cookie;

    if (cookie) {
      const spotify_token = cookie
        .split('; ')
        .find(row => row.startsWith('spotify_token'))
        .split('=')[1];

      setNewCookie(spotify_token);

      console.log({ spotify_token });

      window.parent.postMessage(spotify_token, '*');
    }
  }, [cookieValue]);

  return (
    <div className="App">
      <div>
        <div className="overlay">
          <HeaderBG></HeaderBG>
        </div>
        <div className="contents">
          <header className="App-header">
            <p className="App-name">
              <button className="jumbo btn">
                <a href="#/" className="jumbo">MESSIER ONE</a>
              </button>
            </p>
          </header>
          <Routes />
        </div>
      </div>
      <footer className="App-footer">
        <div style={{ transform: 'scale(1.5)', paddingLeft: '20px' }}>
          <a href="#/">
            <FontAwesomeIcon style={{
              position: 'initial',
              float: 'left',
              left: '0',
            }} icon={faGem} size="sm" />
          </a>
        </div>
        <div>
          <button className="footer-btn btn">
            <a href='#/products'>Products</a>
          </button>
          <button className="footer-btn btn">
            <a href='#/about'>About Us</a>
          </button>
        </div>
      </footer>
    </div>
  );
}

export default App;
